<template>
	<div>
		<div>
			<div class="caozuo">
					<el-input
					  style="width: 200px;"
					  type="textarea"
					  autosize
					  placeholder="ID"
					  v-model="inputID">
					</el-input>
					<el-button type="primary" plain style="padding: 9.5px 10px;margin-right: 3%;"
					 @click="select" ></el-button>
					<el-input
						  style="width: 200px;"
						  type="textarea"
						  autosize
						  placeholder=""
						  v-model="inputCommodity">
					</el-input>	
				<el-button type="primary" plain style="padding: 9.5px 10px;"
				 @click="findByCommodity"></el-button>
				<el-button type="info" plain style="padding: 9.5px 10px; margin-left: 10%;"
				 @click="$router.go(0)"></el-button>
				<!-- <el-button type="success"  plain style="padding: 9.5px 10px;"
				 @click="$router.push('/bokeAddData')"></el-button> -->
				 <el-button type="success" @click="dialogFormVisibles = true"  plain
				 style="margin-left: 5px; padding:9.5px 10px;"></el-button>      
				 <el-dialog title="" :visible.sync="dialogFormVisibles">
				   <el-form>
				    <el-form-item label=":" :label-width="formLabelWidth">
				    <el-input v-model="commodity"  placeholder=""></el-input>
				    </el-form-item>
				    <el-form-item label=":" :label-width="formLabelWidth">
				    <el-input v-model="info" placeholder="" type="textarea">
				    </el-input>
				    </el-form-item>
					<el-form-item label=":" :label-width="formLabelWidth">
					<el-input v-model="price"  placeholder=""></el-input>
					</el-form-item>
					<el-form-item label=":" :label-width="formLabelWidth">
					<el-input v-model="picture"  placeholder=""></el-input>
					</el-form-item>
				   </el-form>
				   <div slot="footer" class="dialog-footer">
				     <el-button @click="dialogFormVisibles = false"> </el-button>
				     <el-button type="primary" @click="addData" :plain="true"> </el-button>
				   </div>
				 </el-dialog>
			<!-- 	<button @click="$router.push('/bokeAddData')"></button> -->
				  <!--  <el-button type="danger"plain style="padding: 9.5px 10px;"
					 @click="pls"></el-button> -->
				
			</div>
			<div class="table-calss">
			<table class="table">
				<tr>
					<td  style="width: 70px;">ID</td>
					<td  style="width: 200px;"></td>
					<td  style="width: 500px;"></td>
					<td  style="width: 450px;"></td>
					<td  style="width: 100px;"></td>
					<td  style="width: 140px;"></td>
				</tr>
			</table>
			<hr />
			<table class="tbale-data">
				<tr v-for="(i,key) in pagelist" :key="key">
					<td style="width: 70px; text-align: center;">{{i.id}}</td>
					<td style="width: 200px;">
						<img :src="i.picture" alt="" style="width: 70px;">
					</td>
					<td style="width: 500px;">
					{{i.commodity}}</td>
					<td style="width: 450px;">{{i.info}}</td>
					<td style="width: 93px;">{{i.price}}</td>
					
				<el-button type="warning" @click="dialogFormVisible = true"  plain
				style="margin-left: 5px; padding:9.5px 10px; margin-top: 25px;"></el-button>      
				<el-dialog title="" :visible.sync="dialogFormVisible">
				  <el-form>
					<el-form-item label="ID:" :label-width="formLabelWidth">
					<el-input v-model="id"  placeholder="ID"></el-input>
					</el-form-item>
					<el-form-item label=":" :label-width="formLabelWidth">
					<el-input v-model="picture"  placeholder=""></el-input>
					</el-form-item>
					<el-form-item label=":" :label-width="formLabelWidth">
				    <el-input v-model="commodity"  placeholder=""></el-input>
					</el-form-item>
				    <el-form-item label=":" :label-width="formLabelWidth">
				      <el-input v-model="info"  placeholder=""></el-input>
				    </el-form-item>
				    <el-form-item label=":" :label-width="formLabelWidth">
				      <el-input v-model="price" placeholder="" type="textarea">
				      </el-input>
				    </el-form-item>
				
				  </el-form>
				  <div slot="footer" class="dialog-footer">
				    <el-button @click="dialogFormVisible = false"> </el-button>
				    <el-button type="primary" @click="update" :plain="true"> </el-button>
				  </div>
				</el-dialog>	
					
				<el-button type="danger"  plain  @click="deletes(i.id)"
				style="margin-left: 5px; padding:9.5px 10px; margin-top: 5px;"></el-button>
				</tr>
				
			</table>			
			</div>
		</div>
		<div class="page">
			<div>
			<el-pagination
			  @current-change="pages"
			  background
			  layout="prev, pager, next"
			  :total="1000">
			</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				page:1,
				pagelist:'',
				dialogFormVisible: false,
				dialogFormVisibles:false,
				formLabelWidth: '120px',
				inputID:'',
				inputCommodity:'',
				picture:'',
				commodity:'',
				info:'',
				price:'',
			    bokename:'',
				bokeinfo:'',
				id:'',
			}
		},
		mounted(){
			axios.get("http://43.142.24.86:8890/api/PageDate",{
				params:{
					'id':this.page
				}
			}).then(page=>{
				this.pagelist=page.data
			})
		},
		methods:{
			findByCommodity(){
				if(this.inputCommodity==''){
					this.$message.error('!');
				}
				if(this.inputCommodity!=''){
				axios.get("http://43.142.24.86:8890/api/selectCommodity",{
					params:{
						'commodity':this.inputCommodity
					}
				}).then(find=>{
					this.pagelist=find.data.data
				})
				}
			},
		
			select(){
				if(this.inputID==''){
					 this.$message.error('ID!');
				}
				if(this.inputID!=''){
				axios.get("http://43.142.24.86:8890/api/selectById",{
					params:{
						'id':this.inputID
					}
				}).then(select=>{
					let arr=[]
					arr.push(select.data.data)
					this.pagelist=arr
				})
				}
				
			},
			pages(pagenum){
				this.page=pagenum
				console.log(pagenum)
				
			},
			addData(){
				
				if(this.commodity==''&&this.info==''){
					 this.$message.error('!');
				}else if(this.picture==''){
					this.$message({
					       message: '',
					       type: 'warning'
					     });
				}else if(this.price==''){
					this.$message({
					       message: '',
					       type: 'warning'
					     });
				}

				if(this.price!=''&&this.commodity!=''&&this.info!=''&&this.picture!=''){					
				axios.post("http://43.142.24.86:8890/api/InsertCommodity",{
					'price':this.price,
					'info':this.info,
					'commodity':this.commodity,
					'picture':this.picture,
				}).then(add=>{
					  this.$message({
					          message: '',
					          type: 'success'
					        });
						this.$router.go(0)
				})
			}
			},
			deletes(id){	
				this.$confirm(', ?', '', {
				      confirmButtonText: '',
				      cancelButtonText: '',
				      type: 'warning',
				      center: true
				    }).then(() => {
				      this.$message({
				        type: 'success',
				        message: '!',
						
				      });
					  axios.get("http://43.142.24.86:8890/api/DeleteById",{
					  	params:{
					  		'id':id
					  	}
					  }).then(dele=>{
					  	this.$router.go(0)
					  })
				    }).catch(() => {
				      this.$message({
				        type: 'info',
				        message: ''
				      });
				    });
			},
			update(){
				axios.post("http://43.142.24.86:8890/api/updateAll",{				
						'id':this.id,
						'commodity':this.commodity,
						'info':this.info,
						'price':this.price,
						'picture':this.picture,
				}).then(update=>{
					console.log(update.data.data)
					if(update.data.data==1){
					 this.$message({
					          message: '',
					          type: 'success'
					        });
							this.$router.go(0)
					}
					if(this.commodity==''&& this.info==''){
						this.$message({
						       message: '',
						       type: 'warning'
						     });
					}else if(this.price==''){
						this.$message({
						       message: '!',
						       type: 'warning'
						     });
						
					}
				
					
				})
			}
		},
		watch:{
			page(){
				axios.get("http://43.142.24.86:8890/api/PageDate",{
					params:{
					'id':this.page,
				}}).then(page=>{
					  this.pagelist=page.data
					  console.log(page.data)
			})
			}
		}
	}
</script>

<style scoped>
	.caozuo{
		background-color:  #e9ebee;
	}
	.table tr td{
		background-color:  #e9ebee;
	}
	.table tr td{
		font-size:20px;
		cursor : pointer;
	}
	.tbale-data tr td{
		font-size:20px;
		width:300px;
		cursor : pointer;
	}
	.tbale-data tr:hover{
		background-color: antiquewhite;
	}
	
	.page div{
		 margin-left: 18%
	}
</style>