<template>
	<div>
		<div class="select"></div>
		<div class="caozuo">
			<el-input
			  style="width: 200px;"
			  type="textarea"
			  autosize
			  placeholder="ID/"
			  v-model="input">
			</el-input>
			<el-button type="primary" plain style="padding: 9.5px 10px;margin-right: 3%;"
			 @click="sousou" ></el-button>
			<el-input
				  style="width: 200px;"
				  type="textarea"
				  autosize
				  placeholder=""
				  v-model="inputs">
			</el-input>	
		<el-button type="primary" plain style="padding: 9.5px 10px;"
		 @click="findBy	"></el-button>
		<el-button type="info" plain style="padding: 9.5px 10px; margin-left: 10%;"
		 @click="$router.go(0)"></el-button>
		<!-- <el-button type="success"  plain style="padding: 9.5px 10px;"
		 @click="$router.push('/bokeAddData')"></el-button> -->
		 <el-button type="success" @click="dialogFormVisibles = true"  plain
		 style="margin-left: 5px; padding:9.5px 10px;"></el-button>      
		 <el-dialog title="" :visible.sync="dialogFormVisibles">
		   <el-form>
		    <el-form-item label=":" :label-width="formLabelWidth">
		    <el-input v-model="username"  placeholder=""></el-input>
		    </el-form-item>
		    <el-form-item label=":" :label-width="formLabelWidth">
		    <el-input v-model="password" placeholder="">
		    </el-input>
		    </el-form-item>
		 	<el-form-item label=":" :label-width="formLabelWidth">
		 	<el-input v-model="emial" placeholder="">
		 	</el-input>
		 	</el-form-item>
		   </el-form>
		   <div slot="footer" class="dialog-footer">
		     <el-button @click="dialogFormVisibles = false"> </el-button>
		     <el-button type="primary" @click="add" :plain="true"> </el-button>
		   </div>
		 </el-dialog>
	<!-- 	<button @click="$router.push('/bokeAddData')"></button> -->
	    <el-button type="danger"plain style="padding: 9.5px 10px;"
		 @click="pls"></el-button>
		
		</div>
		<hr/>
		<div class="table">
			<table id="table">
				<tr>
					<td>	</td>
					<td></td>
					<td></td>
					<td id="td-emailbox"></td>
					<td></td>
				</tr>	
			</table>
			<hr/>
			<table id="table" style="background-color: #fff;">
				
				<tr v-for="(i,key) in pagelist" :label="i.id" :key="key">
					<td>
						<!-- <ul>
							<li v-for="(data,index) of datalist" :key="datalist.id">
							<input type="checkbox" v-model="checkGroup" :value="data.name"/>{{data}}
							</li>
						</ul> -->
						<input type="checkbox" v-model="chengbox" :value="i.id">
						{{i.id}}
					</td>
					<td>
						{{i.username}}
					</td>
					<td>					
						{{i.password}}
					</td>
					<td>
						{{i.emial}}
					</td>
					
					<td id="table_caozuo" >
					      <el-button type="warning" @click="dialogFormVisible = true"  plain
						  style="margin-left: 5px; padding:9.5px 10px;"></el-button>      
					      <el-dialog title="" :visible.sync="dialogFormVisible">
					        <el-form>
							  <el-form-item label="ID:" :label-width="formLabelWidth">
							    <el-input v-model="id"  placeholder="ID"></el-input>
							  </el-form-item>
					          <el-form-item label=":" :label-width="formLabelWidth">
					            <el-input v-model="username"  placeholder=""></el-input>
					          </el-form-item>
					          <el-form-item label=":" :label-width="formLabelWidth">
					            <el-input v-model="password" placeholder="">
					            </el-input>
					          </el-form-item>
							  <el-form-item label=":" :label-width="formLabelWidth">
							    <el-input v-model="emial" placeholder="">
							    </el-input>
							  </el-form-item>
					        </el-form>
					        <div slot="footer" class="dialog-footer">
					          <el-button @click="dialogFormVisible = false"> </el-button>
					          <el-button type="primary" @click="update" :plain="true"> </el-button>
					        </div>
					      </el-dialog>
					
							
						<el-button type="danger"  plain  @click="deletes(i.id)"
						style="margin-left: 5px; padding:9.5px 10px;"></el-button>
					</td>
				</tr>	
			</table>
			
			<!-- <div class="Dataform">
				<ul id="ul1">
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
				</ul>
				<ul v-for="(i,key) in ls" :label="i.id" :key="key" id="ul2">
					<li>{{i.id}}</li>
					<li>{{i.username}}</li>
					<li></li>
					<li></li>
					<li></li>
				</ul>
			</div> -->
		</div>
		     <div class="page">
			 <div>
		     <el-pagination
			 @current-change="pages"
		     background
		     layout="prev, pager, next"
		     :total="100">
		     </el-pagination>
			 </div>
			 </div>
			 </div>
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return {
				input:'',
				inputs:'',
				ls:'',
			    page:1,
				pagelist:'',
				chengbox:[],
				id:'',
		        emial:'',
		        username:'',
		        password:'',
		        dialogFormVisible: false,
				dialogFormVisibles:false,
		        formLabelWidth: '120px'
			}
		},
		//
		methods:{
			//
			add(){
				if(this.username==''&&this.password==''){
					 this.$message.error('!');
				}else if(this.emailbox==''){
					 this.$message.error('!');
				}
				if(this.username!=''&&this.password!=''&&this.emial!=''){
				axios.post("http://43.142.24.86:8890/api/InsertUser",{
					'username':this.username,
					'password':this.password,	
					'emial':this.emial,
				}).then(add=>{			
					if(add.data.data==1){
						this.$message({
						         message: '',
						         type: 'success'
						       });
							this.$router.go(0)
					}
					
				})
				}
			},		
			//
			update(){
				if(this.username==''&& this.password==''){
					 this.$message.error('!');
				}else if(this.id==''){
					this.$message.error('IDID!');
				}else if(this.emial==''){
					this.$message.error('!');
				}
				if(this.username!=''&&this.password!=''&&this.emial!=''){
				axios.post("http://43.142.24.86:8890/api/updateUser",{
					'id':this.id,
					'username':this.username,
					'password':this.password,	
					'emial':this.emial,
				}).then(update=>{
					if(update.data.data==1){
					 this.$message({
					          message: '',
					          type: 'success'
					        });
							this.$router.go(0)
					}
				})
				}
			},
			/*  */
			pages(pagenum){
				this.page=pagenum
				console.log(pagenum)
				
			},
			
			/*  */
			pls(){
				axios.post("http://43.142.24.86:9999/deleteByIds",{			
				   'ids':this.chengbox
				}).then(dis=>{
						this.$router.go(0)				
					})
				},
			/* id */
			sousou(){
				if(this.input==''){
					 this.$message.error('ID');
				}
				if(this.input!=''){
					axios.get("http://43.142.24.86:8890/api/selectByIdUser",{
						params:{
							'id':this.input,		}
						}).then(by=>{
						  let arr=[]
						  arr.push(by.data.data)
						  this.pagelist=arr  
					})
					
					axios.get("http://43.142.24.86:8890/api/selectByUsername",{
						params:{
							'username':this.input
						}
					}).then(username=>{
						this.pagelist=username.data.data
					})
				}/* else{
					axios.post("http://localhost:9633/PageList").then(bys=>{
						  this.pagelist=bys.data
						  
					})
				} */	    
			},
			//
			findBy(){
			if(this.inputs==''){
				this.$message.error('!');
			}
			if(this.inputs!=''){
				axios.get("http://43.142.24.86:8890/api/selectByEmial",{				
						params:{
							'emial':this.inputs
						}
				}).then(findbyEmial=>{				
					this.pagelist=findbyEmial.data.data

				})
			} 
			},
		    /* id*/
			deletes(id){
		    this.$confirm(', ?', '', {
		          confirmButtonText: '',
		          cancelButtonText: '',
		          type: 'warning',
		          center: true
		        }).then(() => {
		          this.$message({
		            type: 'success',
		            message: '!',
					
		          });
				  axios.get("http://43.142.24.86:8890/api/deleteById",{
				  	params:{
				  		'id':id
				  	}
				  }).then(dele=>{
				  	this.$router.go(0)
				  })
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: ''
		          });
		        });
			/* 	axios.get("http://localhost:9633/delete",{
					params:{
						'id':id
					}
				}).then(dele=>{
					this.$router.go(0)
				}) */
				
			}
		},
		
		//
		mounted() {
			/*  */
			axios.get("http://43.142.24.86:8890/api/pageUser",{
				params:{
					'id':this.page,	 
				}
			}).then(page=>{
				this.pagelist=page.data.data
			})
			/*  */
		/* 	axios.get("http://43.142.24.86:9999/selectList").then(ls=>{
				this.ls=ls.data
			}) */
		},
		
		//
		watch:{
			page(){
				axios.get("http://43.142.24.86:8890/api/pageUser",{
					params:{
					'id':this.page,
				}}).then(page=>{
					  this.pagelist=page.data.data
					  console.log(page.data)
			})
			}
		}
	}
</script>

<style scoped>
	 
      #table tr td{
		  font-size:20px;
		  width:300px;
		  cursor : pointer;
	  }
	  #table tr:hover{
		  background-color: antiquewhite;
	  }
	  
	  .caozuo{
		  background-color:  #e9ebee;
	  }
	  .caozuo button{
		  margin-left: 8px;
	  }
   
	
	  .table{
		  float: left;
	  }
	  .page{
		  width: 100%;
		  float: left;
	  }
	  .page div{
		  margin-left: 18%
	/* 	 */
	  }
	  .Dataform ul li{
		  list-style-type: none;
		  width: 20%;
	  }
	  .Dataform #ul1 li{
		  float: left;
	  }
	  .Dataform #ul2 li{
		  
	  }
</style>